<template>
  <div id="app" >
     <div :style="style['chart']['general']" :class="style['chart']['bg']" ref="chart"/>
  </div>
</template>

<script>
import echartOptions from './echats-options.js';
import echarts from "echarts";

export default {
  name: 'App',
  methods: {
    setupMap() {
      this.chart = echarts.init(this.$refs.chart);
      window.onresize = this.chart.resize;
      this.$axios.get('http://47.240.22.222:3456/sina/ncov/news/wap/fymap2020_data.d.json')
        .then((res) => {
          let data = res.data.data.list.sort();
          var d = [], max = 0;
          for (var i = 0; i < data.length; i++) {
            d.push({
              name: data[i].name,
              value: data[i].value,
            });
            max = parseInt(data[i].value) > max ? data[i].value : max;
          }
          echartOptions['series'][0]['data'] = d;
          echartOptions['visualMap']['max'] = max;
          this.chart.setOption(echartOptions);
        });
    },
  },
  mounted() {
    this.setupMap();
  },
  beforeDestroy() {
    if (this.chart) {
      this.chart.dispose();
      this.chart = null;
    }
  },
  data: function() {
    return {
      chart: null,
      style: {
        'chart': {
          'general': {
            'z-index': '-1',
            'position': 'absolute',
            'width': '100%',
            'height': '100%',
            'top': '0',
            'left': '0',
            'overflow-y': 'auto',
          },
          'bg': 'fly_high',
        },
      },
    };
  },
}
</script>
